.Category {
  width: 14%;
  padding: 10px;
  background: white;
  border-radius: 10px;
  margin: 20px;
  font-weight: 700;
  border: 3px solid darkblue;
  font-size: 1.5rem;
  -webkit-box-shadow: 0 8px 6px -6px black;
          box-shadow: 0 8px 6px -6px black;
  color:  darkblue;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.CategorySelected {
  width: 14%;
  padding: 7px;
  background: white;
  border-radius: 10px;
  margin: 20px;
  font-weight: 700;
  border: 6px solid coral;
  font-size: 1.5rem;
  -webkit-box-shadow: 0 8px 6px -6px black;
          box-shadow: 0 8px 6px -6px black;
  color:  darkblue;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}



