.MainContent {
  padding-top: 5vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  font-family: inherit;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.SelectLevel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-animation: slowShow 0.8s ease;
          animation: slowShow 0.8s ease;
}

.SelectCategory {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  -webkit-animation: slowShow2 0.8s ease;
          animation: slowShow2 0.8s ease;
}

.Controls {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

.MissingGameButtons {
  border: 5px solid #1857BC;
  background-color: #FFE44F;
  color: #FF5805;
  border-radius: 25px
}

.CardsContainer {
  -webkit-animation: slowShow 0.8s ease;
          animation: slowShow 0.8s ease;
}

/* The animation code */
@-webkit-keyframes slowShow {
    from {opacity: 0}
    to {opacity: 1}
}
@keyframes slowShow {
    from {opacity: 0}
    to {opacity: 1}
}

@-webkit-keyframes slowShow2 {
    from {opacity: 0}
    to {opacity: 1}
}

@keyframes slowShow2 {
    from {opacity: 0}
    to {opacity: 1}
}




