.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    background: -webkit-gradient(linear , left top, left bottom , from(#ddd6f3) , color-stop(white), color-stop(white), color-stop(white), to(#ddd6f3));
    background: -webkit-linear-gradient(#ddd6f3 , white, white, white, #ddd6f3);
    background: -o-linear-gradient(#ddd6f3 , white, white, white, #ddd6f3);
    background: linear-gradient(#ddd6f3 , white, white, white, #ddd6f3);
    width: 70%;
    max-height: 80%;
    border: 1px solid #ccc;
    -webkit-box-shadow: 1px 1px 1px black;
            box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 10%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    border: 5px solid #FF1493;
    overflow-x: hidden
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}


